import { Link } from "react-router-dom"
import { DUMMY_USER_URL } from "../../config/config"

const ContactInfo = ({ contact }) => {
    return (
        <>
            <div className="list-product__image">
                <Link to={process.env.PUBLIC_URL + `/contact/${contact._id}`}>
                    <img
                        src={contact?.img?.url || DUMMY_USER_URL}
                        className="img-fluid img__circle"
                        alt=""
                    />
                </Link>
            </div>

            <div className="list-product__content">
                <h3 className="title">
                    <Link to={process.env.PUBLIC_URL + `/contact/${contact._id}`}>
                        {contact.category}
                    </Link>
                </h3>
                <span className="category">
                    {contact.company}
                </span>
                <span className="category">{contact.jobTitle}</span>
            </div>
        </>
    )
}

export default ContactInfo