import { Agent } from "../agent/Agent"
import { Email } from "../email/Email"
import { constContact } from "./constContact"


export class Contact {
    _id
    desc
    img
    jobTitle
    price
    inStock
    name
    familyName
    emails = []
    mobile
    phone
    linkedinLink
    agent
    category
    country
    company
    createdAt
    transactionHistory

    constructor(contact) {
        this._id = contact[constContact.ID]
        this.desc = contact[constContact.DESCRIPTION]
        this.img = contact[constContact.IMG]
        this.jobTitle = contact[constContact.JOB_TITLE]
        this.price = contact[constContact.PRICE]
        this.inStock = contact[constContact.IN_STOCK]
        this.name = contact[constContact.NAME]
        this.familyName = contact[constContact.FAMILY_NAME]
        this.mobile = contact[constContact.MOBILE]
        this.phone = contact[constContact.PHONE]
        this.linkedinLink = contact[constContact.LINKEDIN_LINKS]
        this.category = contact[constContact.CATEGORY]
        this.country = contact[constContact.COUNTRY]
        this.company = contact[constContact.COMPANY]
        this.createdAt = contact[constContact.CREATED_AT]
        this.transactionHistory = contact[constContact.TRANSACTION_HISTORY]
        this.agent = contact[constContact.AGENT] ? new Agent(contact[constContact.AGENT]) : null
        this.emails = contact[constContact.EMAILS] || []
    }
}