import React, { Fragment } from "react";
import { Link } from "react-router-dom";
import { useDispatch, useSelector } from "react-redux";
import { ReactSVG } from "react-svg";
import { Breadcrumb } from "../../components";
import ContactInfo from "../../components/Contact/ContactInfo";
import { RiDeleteBin6Line } from 'react-icons/ri'
import { deleteAllFromCartAction, removeContactToCartAction } from "../../store/cart/cart-action";
import cogoToast from "@hasanm95/cogo-toast";
import { utilService } from "../../services/util.service";
import { createContactsTransaction } from "../../store/payment/contact.payment-action";
import { ConstTranslate } from "../../constants/translate";
import LoadingButton from "../../components/Core-Components/LoadingButton/LoadingButton";
import { useState } from "react";

const Cart = () => {
  const { contactCart } = useSelector((state) => state.cart);
  const { currentUser } = useSelector((state) => state.user)
  const [isLoading, setIsLoading] = useState(false)
  const dispatch = useDispatch()

  const getCartTotalPrice = () => {
    if (!contactCart.length) return Number(2).toFixed(2)
    return contactCart.reduce((acc, contact) => acc += contact.price, 0).toFixed(2)
  }

  const onRemove = (contactId) => {
    removeContactToCartAction(dispatch, contactId)
  }

  const onPurchase = async () => {
    setIsLoading(true)
    if (!isUserHasCredits()) {
      setIsLoading(false)
      return cogoToast.error("Not enough credits", { position: "top-center" })
    }

    try {
      await createContactsTransaction(dispatch, contactCart)
      deleteAllFromCartAction(dispatch)
      return cogoToast.success("Contact purchased", { position: "top-center" })
    } catch (err) {
      return cogoToast.error("Cannot purchase contact", { position: "top-center" })
    } finally {
      setIsLoading(false)
    }
  }

  const isUserHasCredits = () => {
    const sum = contactCart.reduce((acc, contact) => acc += contact.price, 0)
    if (currentUser.credits >= utilService.getContactValueInCredit(sum)) return true
    else return false
  }

  return (
    <div className="body-wrapper space-pb--120 backgrounds__white-top">
      <Breadcrumb pageTitle="Cart" />
      {contactCart && contactCart.length >= 1 ? (
        <Fragment>
          {contactCart && contactCart.length >= 1 && contactCart.map((contact) => {
            return (
              <div key={contact._id} className="list-product border-bottom--medium">
                <ContactInfo contact={contact} />

                <div className="flex align-center gap--25">
                  <div className="list-product__price">
                    <h2>{contact.price}$</h2>
                    <p>22.07.23</p>
                  </div>

                  <button className="list-product__button icon-color__error" onClick={() => onRemove(contact._id)}>
                    <RiDeleteBin6Line />
                  </button>
                </div>
              </div>
            )
          })}

          <div className="grand-total">
            <div className="container">
              <div className="row">
                <div className="col-12">
                  <h4 className="grand-total-title">
                    Credits: <span>{currentUser.credits}$</span>
                  </h4>
                  <h4 className="grand-total-title">
                    Total Contacts: <span>{contactCart.length}</span>
                  </h4>
                  <h4 className="grand-total-title">
                    Total Price: <span>{getCartTotalPrice()}$</span>
                  </h4>
                  {/* <Link to={process.env.PUBLIC_URL + "/checkout"}>
                    Proceed to Checkout
                  </Link> */}
                  <LoadingButton className={'button button__h-60'} onClick={onPurchase} isLoading={isLoading}>
                    {ConstTranslate.PROCEED_TO_CHECKOUT}
                  </LoadingButton>
                </div>
              </div>
            </div>
          </div>
        </Fragment>
      ) : (
        <div className="no-items-found">
          <div className="no-items-found__image">
            <ReactSVG
              src={process.env.PUBLIC_URL + "/assets/img/icons/cart.svg"}
            />
          </div>
          <div className="no-items-found__content">
            <p>
              No Items in the cart.{" "}
              <Link to={process.env.PUBLIC_URL + "/shop"}>Add Some</Link>
            </p>
          </div>
        </div>
      )}
    </div>
  );
}


export default Cart;
