import { CreditTransactionEP } from "../config/config"
import { CreditTransaction } from "../model/creditTransaction/CreditTransaction"
import { httpService } from "./http.service"
import { userService } from "./user.service"


export const creditTransactionService = {
    query,
    createCreditTransaction,
}

async function query(filterBy = {}) {
    try {
        const res = await httpService.get(CreditTransactionEP.default, filterBy)
        if (res.status === 'ok') return res.content
        else return { error: 'Cannot get credits transactions' }
    } catch (err) {
        throw err
    }
}

async function createCreditTransaction(credit) {
    const content = { user: userService.getLoggedinUser(), credit, type: 'credit_purchase' }
    return _createCreditTransaction(content)
}

function _createCreditTransaction({ user, credit, type }) {
    return new CreditTransaction({ credit, user, type })
}